"use client"; 

import LoadingComponent from '@/components/Loading';
import { cameraFeeds } from '@/data/videos';
import { CameraFeed } from '@/model/videosModel';
import { useParams } from 'next/navigation';
import { useState, useEffect } from 'react';

const CameraPage = () => {
  const [camera, setCamera] = useState<CameraFeed | null>(null);
  const [loading, setLoading] = useState(true); // State để quản lý loading
  const { cameraName } = useParams(); 

  useEffect(() => {
    if (cameraName && typeof cameraName === 'string') {
      const cameraId = parseInt(cameraName);
      const camFind = cameraFeeds.filter(cam => cam.id === cameraId);
      if (camFind.length > 0) {
        setCamera(camFind[0]);
      } else {
        setCamera(null); 
      }
    }

    setLoading(false);
  }, [cameraName]); 

  if (loading) {
    return <LoadingComponent/>;
  }

  return (
    <div>
      <h1>Trang camera: {camera?.name || 'Không tìm thấy camera'}</h1>
      {camera && (
        <div>
          <p>Thông tin chi tiết về camera: {JSON.stringify(camera)}</p>
        </div>
      )}
    </div>
  );
};

export default CameraPage;
