import React from "react";

const LoadingComponent = () => {
  return (
    <div className="flex justify-center items-center min-h-screen bg-gradient-to-r from-indigo-500 via-purple-500 to-pink-500">
      <div className="relative flex justify-center items-center">
        <div className="absolute animate-spin-slow w-24 h-24 rounded-full border-4 border-t-transparent border-white opacity-60"></div>
        <div className="absolute animate-spin-slow-reverse w-32 h-32 rounded-full border-4 border-t-transparent border-white opacity-40"></div>
        <div className="absolute animate-spin-slow w-40 h-40 rounded-full border-4 border-t-transparent border-white opacity-20"></div>
        <div className="w-10 h-10 bg-white rounded-full shadow-xl animate-pulse"></div>
      </div>
    </div>
  );
};

export default LoadingComponent;
